<template>
   <div class="password">
        <van-cell-group class='top_but'>
            <van-field v-model="phone" style='background:#f5f5f5; ' 
            @change='checkphone' 
            placeholder="请输入手机号码"/>
        </van-cell-group>
        <span ref='phone'>{{masphone}}</span>
        <van-cell-group class='top_but'>
            <van-field style='background:#F5F5F5' type="password" placeholder="请输入密码" />
        </van-cell-group>
        <span></span>
   </div>
</template>

<script>
export default {
    name: '',
    data() {
        return {
            masphone:'',
            phone:''
        }
    },
    methods:{
        checkphone:function(){
            var myreg = /^1[34578]\d{9}$/; 
            if(this.phone==""){
                this.masphone='手机号不能为空'
            }else if(!myreg.test(this.phone)){
                this.masphone='请输入正确的手机号'
            }else{
                this.masphone="输入正确"
            }
        },
    },
    components: {},
    watch:{
    }
}
</script>

<style scoped lang='scss'>
span{
    font-size: 6px;
    padding: 0 8px;
}
</style>
