<template>
   <div class="Login">
       <div class="mine_top">
            <p>登录</p>
            <!-- <transition name='fold'> -->
                <components :is='comName'></components>
            <!-- </transition> -->
            <div class='pass_bot'>
                <p @click="comName='Code'">验证码登录</p>
                <!-- <p style='text-align:right' @click="comName='Reset'">忘记密码</p> -->
            </div>
            <div 
            v-show='flag' 
            @click='warning()'
            class='warning'>
                请输入正确的验证码
            </div>
       </div>
       <div class="mine_bot">
            <!-- <p @click="comName='Mailbox'">用邮箱登录</p> -->
            <van-button 
            class='bot_but' 
            style='background:#ff69b4;margin: 3px 0;color:white;'
            @click='landing()' 
            size="large"
            >登录</van-button>
            <p>继续即表示您同意《》和《》</p>
       </div>
   </div>
</template>

<script>
import Password from './Password'
import Code from './Code'
import Mailbox from './mailbox'
import Reset from './Resetpass'
import axios from 'axios'

export default {
    components: { Password,Code,Mailbox,Reset },
    name: 'Login',
    data() {
        return {
            comName:'Code',
            flag:false,
        }
    },
    methods:{
        landing(){
            // console.log(this);
            if( this.$children[0].phone === '' ){
                this.$dialog.alert({
                    title: '提示',
                    message: '请输入手机号码！'
                });
            }else if( this.$children[0].code === '' ){
                this.$dialog.alert({
                    title: '提示',
                    message: '请输入手机验证码！'
                });
            }else{
                let that = this;
                let url = window.globalData.config.domain + '/user/registerWithCode';
                axios.post(url,{
                    cmd: '1',
                    body:{
                        phone: this.$children[0].phone,
                        did: '1',
                        appid: '11',
                        op: 'register',
                        sid: this.$children[0].res.sid,
                        code: this.$children[0].code,
                    }
                }).then((res) => {
                    let data = res.data;
                    // console.log(data)
                    if( data.code === 103 ){
                        this.$dialog.alert({
                            message: '验证码输入错误！'
                        });
                    }else if( data.code === 0 ){
                        localStorage.setItem('token',data.body.accessToken);
                        localStorage.setItem('phone',data.body.phone);
                        window.globalData.config.accessToken = data.body.accessToken;
                        this.$router.push('/');
                    }
                })
            }
        },
        warning(){
            this.flag = false;
        }
    }

}
</script>

