<template>
   <div class="Code" ref='code'>
         <van-cell-group class='top_but'>
            <van-field style='background:#F5F5F5' 
            class="phone"
            v-model="phone"
            ref="phone"
            @change='checkphone'
            placeholder="请输入手机号" />
         </van-cell-group>
         <span>{{masphone}}</span>
         <van-cell-group class='top_but'>
            <van-field
               center
               clearable
               v-model="code"
               placeholder="请输入短信验证码"
               style='background:#F5F5F5'>
               <van-button 
               @click='setCode()' 
               class='getcode'
               slot="button" 
               size="small" 
               :disabled='!flag'
               type="primary">
               获取验证码<span v-show="!show">({{count}})</span>
               </van-button>
            </van-field>
         </van-cell-group>
         <span></span>
   </div>
</template>

<script>

export default {
   name: '',
   data() {
       return {
            masphone:'',
            phone:'',
            flag: true,
            code: '',
            res:'',
            show: true,
            count: '',
            timer: null,
       }
   },
   watch:{
      phone(New,Old){
         if(!/^[0-9]*$/.test(New)){
            this.$dialog.alert({
               message: '请输入正确手机号'
            })
            this.phone = '';
         }else if(this.phone === " "){
            this.masphone='手机号不能为空';
            this.$el.style.color = 'red';
            this.flag = false;
            // console.log(this.phone)
         }else if(!myreg.test(this.phone)){
            this.masphone='请输入正确的手机号';
            this.$el.style.color = 'red';
            this.flag = false;
         }else{
            this.masphone="输入正确";
            this.$el.style.color = 'green';
            this.flag = true;
         }
      }
   },
   methods:{
      checkphone:function(){
         var myreg = /^1[345678]\d{9}$/; 
         
      },
      setCode(){
         if( this.flag == true && this.phone !== ''){
            const TIME_COUNT = 60;
            if( this.show == true ){
               let url = 'guard/vcode';
               this.$http.post(url,{
                  phone:this.phone,
                  did:'1',
                  appid:'11',
                  op:"register"
               }).then((res) => {
                  this.res = res;
                  // console.log(this.res);
               })
            }
            if (!this.timer) {
               this.count = TIME_COUNT;
               this.show = false;
               this.flag = false;
               this.timer = setInterval(() => {
                  if (this.count > 0 && this.count <= TIME_COUNT) {
                     this.count--;
                  } else {
                     this.show = true;
                     this.flag = true;
                     clearInterval(this.timer);
                     this.timer = null;
                  }
               }, 1000)
            }
         }
      },
   },
   components: {}
}
</script>

<style scoped lang='scss'>
.Code{
   font-size: 18px;
}
.getcode{
   font-size: 18px;
   background:#ff6347; 
   border:none; 
   width:128px; 
   text-align:center;
}
.top_but{
    margin-bottom: 8px;
}
.but2{
   width: 60%;
}
span{
    font-size: 6px;
    padding: 0 8px;
}
</style>
